 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 600px) {
    header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 10% 0 10%;
      }
      
      svg {
        height: 70px;
        width: auto;
      }
      
      .sticky {
        position: fixed;
        top: 0;
        width: 100%
      }
      
      .header.container {
        display: none;
      }
      
      .header.right {
        margin-left: 150px;
        font-family: 'Noto Sans', sans-serif;
        font-weight: 600;
        letter-spacing: 0.07em;
      }
    
      ul.header {
        list-style: none;
      }
    
      li.header {
        float: left;
        padding-right: 100px;
      }
      
      .head {
        color: black;
        transition-duration: 0.5s;
      }
      
      .head:hover {
        color: black;
        transition-duration: 0.5s;
        transform: translateY(-4px);
      }
    
    
      .hamburger-container {
        width: 100%;
        display: flex;
      }
      
      .hamburger-right {
        font-family: 'Noto Sans', sans-serif;
        font-weight: 600;
        letter-spacing: 0.07em;
      }
    
      .burger {
          height: 40px;
          width: auto;
      }
    
      .hamburger {
          display: none;
      }
    
      ul.hamburger {
        list-style: none;
      }
    
      li.hamburger {
        float: left;
        padding-right: 100px;
      }
      
      .head {
        color: black;
        transition-duration: 0.5s;
      }
      
      .head:hover {
        color: black;
        transition-duration: 0.5s;
        transform: translateY(-4px);
      }
 }

