 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 600px) {
    .skills {
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        letter-spacing: 0.07em;
        font-family: 'Noto Sans', sans-serif;
        text-shadow: -1px -1px #ffffff;
      }
      
      .skills-container {
        margin-left: 5%;
        margin-right: 5%;
        margin-bottom: 10%;
      }
      
      .skills-content {
        display: flex;
        justify-content: center;
        width: 90vw;
        background-color: rgb(235, 240, 243);
        border: 1px solid rgba(255, 255, 255, 0.350);
        border-radius: 5px;
        box-shadow: 0 4px 35px rgba(0, 0, 0, 0.2);
      }
      
      .skills-list {
        padding: 2% 8% 2% 8%;
      }
      
      .skill {
        display: flex;
        align-items: center;
        padding: 1vw;
        text-align: left;
        font-size: 1.2em;
        letter-spacing: 0.07em;
      }
    
      .skill-name {
          padding-left: 20px;
      }
      
      .about {
        padding: 1vw;
        text-align: left;
        font-size: 1.2em;
        letter-spacing: 0.07em;
      }
      
      .about-link {
        color: black;
        text-decoration: underline;
      }
 }

