 /* Extra small devices (phones, 600px and down) */
 @media only screen and (max-width: 600px) {
    .footer-container {
        margin-top: 60px;
        width: 100%;
        display: flex;
        flex-direction: column;
        background: linear-gradient(144deg, rgba(44,43,38,1) 0%, rgba(28,27,23,1) 100%);
        border-top: 1px solid rgb(134, 134, 134);
        border-bottom: 1px solid rgb(24, 24, 24);
        box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
      }
      
      .footer-in {
        color: #dde0e9;
        margin: 2% 0% 2% 0%;
        font-family: 'Noto Sans', sans-serif;
        font-size: 1.2em;
        text-shadow: 2px 2px black;
        letter-spacing: 0.07em;
      }
      
      li {
        display: flex;
        align-items: center;
        list-style: none;
        margin-bottom: 25px;
      }

      .foot-svg {
          display: none;
      }
 }

 /* Small devices (portrait tablets and large phones, 600px and up) */
 @media only screen and (min-width: 600px) {}
 
 /* Medium devices (landscape tablets, 768px and up) */
 @media only screen and (min-width: 768px) {
    .footer-container {
        margin-top: 60px;
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        background: linear-gradient(144deg, rgba(44,43,38,1) 0%, rgba(28,27,23,1) 100%);
        border-top: 1px solid rgb(134, 134, 134);
        border-bottom: 1px solid rgb(24, 24, 24);
        box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
      }
      
      .footer-in {
        color: #dde0e9;
        margin: 2% 0% 2% 0%;
        font-family: 'Noto Sans', sans-serif;
        font-size: 1.2em;
        text-shadow: 2px 2px black;
        letter-spacing: 0.07em;
      }
      
      li {
        display: flex;
        align-items: center;
        list-style: none;
        margin-bottom: 25px;
      }

      .foot-svg {
          display: block;
      }
 }
 
 /* Large devices (laptops/desktops, 992px and up) */
 @media only screen and (min-width: 992px) {}
 
 /* Extra large devices (large laptops and desktops, 1200px and up) */
 @media only screen and (min-width: 1200px) {
    .footer-container {
        margin-top: 150px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background: linear-gradient(144deg, rgba(44,43,38,1) 0%, rgba(28,27,23,1) 100%);
        border-top: 1px solid rgb(134, 134, 134);
        border-bottom: 1px solid rgb(24, 24, 24);
        box-shadow: 0 0px 50px rgba(0, 0, 0, 0.5);
      }
      
      .footer-in {
        color: #dde0e9;
        margin: 2% 5% 2% 5%;
        font-family: 'Noto Sans', sans-serif;
        font-size: 1.2em;
        text-shadow: 2px 2px black;
        letter-spacing: 0.07em;
      }
      
      li {
        display: flex;
        align-items: center;
        list-style: none;
        margin-bottom: 25px;
      }
 } 

